import './index.scss';

import classNames from 'classnames';
import { HTMLAttributes, ReactNode, useEffect } from 'react';

// ----------------------------------------------------------------------

export type SectionProps = {
  title?: string;
  children: ReactNode;
  alignItems?: 'start' | 'center' | 'end' | 'stretch';
  respond?: boolean;
  className?: string;
} & HTMLAttributes<HTMLBaseElement>;

const ALIGN_MAPS = {
  start: 'qb-section--items-start',
  center: 'qb-section--items-center',
  end: 'qb-section--items-end',
  stretch: 'qb-section--items-stretch'
};

const Section = ({
  title = 'FA System',
  children,
  respond = false,
  alignItems = 'center',
  className
}: SectionProps) => {
  useEffect(() => {
    document.title = title;
  }, [title]);

  return (
    <div
      className={classNames(
        'qb-section',
        { 'qb-section--respond': respond },
        ALIGN_MAPS[alignItems],
        className
      )}
    >
      {children}
    </div>
  );
};

export default Section;
